
#content {
  margin: 0 auto;
  text-align: center;
}
#menu {
  color: #b2b2b2;
  font-size: 200%;
  position: absolute;
  top: 10px;
  left: 20px;
}
#status {
  font-size: 300%;
  position: absolute;
  top: -3px;
  right: 20px;
}
.red {
  color: var(--red);
}
.green {
  color: var(--green);
}
@font-face {
  font-family: Barlow;
  src: url(/static/fonts/barlow.ebd90d8.ttf);
}
@media (min-width: 500px) {
#app {
    width: 50vh;
    margin: auto;
}
}
:root {
  --primary: #0087E8;
  --secondary: #6B6570;
  --background: #ededed;
  --dark: #282727;
  --light: #fff;
  --red: #f10032;
  --green: #47cf73;
}
* {
  font-family: Barlow, 'PT Sans', sans-serif;
  outline: none;
}
body {
  background: var(--background);
  padding: 0;
  width: 100%;
  margin: 0 auto;
}
img {
  display: block;
  margin: 0 auto;
  width: 69.5%;
  height: auto;
}
.fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.fade-leave-active {
  -webkit-transition: opacity 0s;
  transition: opacity 0s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
}

























































































/* remove outlines from form */
form input[data-v-27b67ac9]:focus,
form button[data-v-27b67ac9]:focus {
  outline: none;
}
